#modal-wrapper {
    background-color: var(--alternate-translucent);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: fixed;
    z-index: 100000;
}

#modal {
    background-color:var(--main-background);
    border: 2px solid var(--main-font);
    border-radius: 4px;
    box-sizing: border-box;
    max-height: 90vh;
    margin: auto;
    overflow-y: auto;
    padding: 30px;
    position: absolute;
    top: 5vh;
    left: 0;
    right: 0;
    width: 800px;
}

#close-button {
    position: absolute;
    padding: 30px;
    right: 0;
    top: 0;
}